html {
  font-size: 100%;
  box-sizing: border-box;
  font-family: var(--font-inter);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  height: 100vh;
  background-color: var(--very-dark-blue);
  margin: 0;
  padding: 0;
}

:root {
  --very-dark-blue: hsl(233, 47%, 7%);
  --dark-saturated-blue: hsl(244, 38%, 16%);
  --soft-violet: hsl(277, 64%, 61%);
  --white: hsl(0, 0%, 100%);
  --slightly--transparent-white--p: hsla(0, 0%, 100%, 0.75);
  --slightly--transparent-white--h: hsla(0, 0%, 100%, 0.6);
}

:root {
  --font-inter: "Inter", sans-serif;
  --font-lexend: "Lexend Deca", sans-serif;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

.hero {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 7rem;
}
.hero__container {
  background-color: var(--dark-saturated-blue);
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  min-width: 80em;
}
.hero__container .card {
  flex: 1;
  padding: 4rem 0 5rem 4rem;
}
.hero__container .card .title {
  font-size: 2.4rem;
  color: var(--white);
}
.hero__container .card .title span {
  color: var(--soft-violet);
}
.hero__container .card .description {
  width: 24em;
  margin-top: 2rem;
  color: var(--slightly--transparent-white--p);
  font-size: 1rem;
  line-height: 1.7;
}
.hero__container .card .stats {
  margin-top: 5rem;
  width: 24em;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.hero__container .card .stats .stat h2 {
  color: var(--white);
}
.hero__container .card .stats .stat p {
  margin-top: 0.3rem;
  font-size: 0.8rem;
  font-family: var(--font-lexend);
  text-transform: uppercase;
  color: var(--slightly--transparent-white--h);
}
.hero__container .card .stats .third {
  text-transform: uppercase;
}
.hero__container .image {
  position: relative;
  flex: 1;
  background: red;
}
.hero__container .image img {
  height: 100%;
  width: 100%;
}
.hero__container .image .layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--soft-violet);
  opacity: 0.82;
}